@font-face {
  font-family: "JetBrainsMono";
  src: url('https://cdn.just-plain.fun/static/JetBrainsMono-Regular.ttf');
}

@font-face {
  font-family: "Source Han Serif CN";
  src: url('https://cdn.just-plain.fun/static/SourceHanSerifCN-Regular-min.otf');
}

@font-face {
  font-family: "Source Han Serif CN";
  src: url('https://cdn.just-plain.fun/static/SourceHanSerifCN-Bold-min.otf');
  font-weight: bold;
}

@font-face {
  font-family: "Cantarell";
  src: url('https://cdn.just-plain.fun/static/Cantarell-VF-fixed.otf');
}

body,
textarea {
  /* background-color: #fcfdff; */
  margin: 0;
  /* text-align: justify; */
  font-family: Cantarell, 'Source Han Serif CN';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #40464f;
}

code {
  font-family: 'JetBrainsMono', source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
  text-decoration: none;
}

::-webkit-scrollbar-thumb {
  border-radius: 2rem;
  background: #d7dde7af;
}

::-webkit-scrollbar {
  width: .6rem !important;
}

::selection {
  background: #c8d2e183;
}